export default{
	name:"Item",
	data(){
		return {
			id:this.$store.state.listId,
			content:""
		}
	}
	,
	mounted(){
		document.documentElement.scrollTop=0
		var _this=this
	this.$http.get("https://www.xueguoguo.cn/wxapi/Article?funcid=info&id="+_this.id,{}).then(function(result){
		console.log(result.data)
		_this.content=result.data.result[0].content
	})
		
	},methods:{
		
	}
}



