export default{
	name:"List",
	data(){
		return{
			list:[]
	
		}
	},
	mounted(){
		var _this=this
		this.$http.get("https://www.xueguoguo.cn/wxapi/Article?funcid=list",{})
		.then(function(result){
			console.log(result.data)
			_this.list=result.data.list
		})
	},
	methods:{
		goEach(id){
			this.$store.state.listId=id
			console.log(this.$store.state.listId)
			this.$router.push({
				path: '/Item'+"?"+this.$store.state.listId
			})
		}
	}
}



